html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  /* Propiedad que nos permite una navegación más suavizada */
  scroll-behavior: smooth;
}

/* Propiedad que define la posición en donde se detendrá al alcnazar el ID */
section[id=project-section] {
  scroll-margin-top: 4rem;
}

section[id=inicio-section] {
  scroll-margin-top: 6.5rem;
}

body {
  font-family: "Signika", sans-serif;
  font-size: 1.8rem;
  color: white;
  background-color: rgb(173, 173, 173);
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 2;
  margin: 0;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1, h2, h3 {
  font-family: "Signika", sans-serif;
  margin: 0 0 2.5rem 0;
  text-transform: uppercase;
  text-align: center;
  line-height: 1.5;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2.8rem;
}

.header {
  background-color: #050505;
  position: sticky;
  top: 0;
  z-index: 30;
  padding: 0.5rem 0;
}
.header .navbar {
  display: flex;
  justify-content: center;
  padding: 1.5rem;
  position: sticky;
  top: 0;
  z-index: 40;
}
@media (min-width: 768px) {
  .header .navbar {
    justify-content: flex-end;
  }
}
.header .navbar ul {
  display: flex;
  list-style: none;
}
.header .navbar ul a {
  font-size: 2rem;
  margin: 2rem;
  color: white;
}
.header .navbar ul a:hover {
  border-bottom: #18db86 solid;
}

/* Profile Section */
.hero {
  background-color: #050505;
  position: relative;
}
.hero img {
  width: 100%;
  height: 53rem;
  opacity: 0.35;
}

span {
  color: #18db86;
  cursor: default;
}

.profile, .profile .contact-icon {
  display: flex;
  justify-content: center;
  /* Square-Text */
}
.profile div, .profile .contact-icon div {
  position: absolute;
}
.profile .square {
  width: 32rem;
  height: 45rem;
  background-color: #222222;
  opacity: 0.7;
  z-index: 10;
  top: 7rem;
}
.profile .circle-trans {
  width: 29rem;
  height: 29rem;
  border-radius: 50%;
  border: solid 3rem #18db86;
  opacity: 0.2;
  z-index: 19;
  top: 9rem;
}
.profile .circle {
  width: 23rem;
  height: 23rem;
  border-radius: 50%;
  border: solid 1.5rem #18db86;
  z-index: 21;
  top: 12rem;
}
.profile .profile-image {
  top: 13.4rem;
  z-index: 29;
}
.profile .profile-image img {
  width: 20.2rem;
  height: 20.2rem;
  border-radius: 50%;
}
.profile .square-text-1 {
  font-size: 2rem;
  color: white;
  top: 39rem;
  z-index: 11;
}
.profile .square-text-2 {
  font-size: 1.4rem;
  color: white;
  top: 41.8rem;
  z-index: 11;
}
.profile .square-text-3 {
  font-size: 1.4rem;
  color: white;
  top: 49.5rem;
  z-index: 11;
  transition-property: border-bottom transform;
  transition: 0.2s ease;
}
.profile .square-text-3 a {
  color: white;
}
.profile .square-text-3:hover {
  border-bottom: #18db86 solid;
  transform: scale(1.1);
}
.profile .square-text-1,
.profile .square-text-2,
.profile .square-text-3 {
  text-transform: uppercase;
}
.profile .contact-icon {
  top: 44rem;
  z-index: 11;
}
.profile .contact-icon img {
  width: 4rem;
  margin: 0 1rem;
  transition-property: transform;
  transition: 0.4s;
  cursor: pointer;
}
.profile .contact-icon img:hover {
  transform: scale(1.2);
}

/* Briefcase */
.briefcase {
  background-color: #222222;
  padding-bottom: 5rem;
}
.briefcase h2 {
  padding: 4rem;
}
.briefcase .briefcase-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(29rem, auto));
  gap: 1.5rem;
  width: 90%;
  margin: 0 auto;
}
.briefcase .briefcase-grid img {
  padding: 1rem;
  width: 100%;
  border-radius: 2rem;
  transition-property: transform;
  transition: 0.2s ease;
  cursor: pointer;
}
.briefcase .briefcase-grid img:hover {
  transform: scale(1.1);
}

/* Modals */
.eins-modal-content {
  max-width: 70rem;
  background-color: #222222;
}

.pw-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, auto));
}
.pw-content .eins-modal-preview {
  max-width: 35rem;
  margin: 0 auto;
}
.pw-content .eins-modal-preview img {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
  cursor: zoom-in;
}
@media (min-width: 724px) {
  .pw-content .eins-modal-preview img {
    padding: 2rem 4rem 2rem 0;
    margin-bottom: 0;
  }
}

@media (max-width: 599px) {
  .eins-modal-text-1 {
    grid-row: 1/3;
    margin-bottom: 2rem;
  }
}
.eins-modal-text-1 h3 {
  color: white;
  margin-top: 2rem;
}
.eins-modal-text-1 p {
  text-align: center;
  font-size: 1.5rem;
  color: white;
  padding: 1rem;
}
.eins-modal-text-1 .eins-modal-text-2 {
  padding: 1rem;
}
.eins-modal-text-1 .eins-modal-text-2 a {
  color: rgb(173, 173, 173);
  margin: 1rem;
}
.eins-modal-text-1 .eins-modal-text-2 a span {
  display: block;
  margin-top: 1rem;
}
.eins-modal-text-1 .eins-modal-text-3 {
  padding: 0 1rem 1rem;
}
.eins-modal-text-1 .eins-modal-text-3 .eins-modal-tec {
  padding: 1rem 0;
  color: #18db86;
}
.eins-modal-text-1 .eins-modal-text-3 .eins-modal-tec-icons {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 1rem;
}
.eins-modal-text-1 .eins-modal-text-3 .eins-modal-tec-icons img {
  align-items: center;
  width: 6.5rem;
  height: 5rem;
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}
.eins-modal-text-1 .eins-modal-text-3 .eins-modal-tec-icons img:hover {
  transform: scale(1.2);
}

#nucleus .eins-modal-tec-icons img:nth-child(1), #arquitecturaBosque .eins-modal-tec-icons img:nth-child(1) {
  grid-column: 2/3;
}

/* Skills */
.skills {
  background-color: #222222;
}
.skills .skill {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, auto));
  justify-content: center;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 11rem;
  gap: 8rem;
}
@media (min-width: 768px) {
  .skills .skill {
    grid-template-columns: repeat(auto-fit, minmax(30rem, auto));
  }
}
.skills .skill h2 {
  padding-bottom: 6rem;
}
.skills .skill img {
  width: 9rem;
  cursor: pointer;
}
.skills .knowledge, .skills .tools-part .tools {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(11rem, auto));
  justify-content: center;
  column-gap: 2.5rem;
}
@media (min-width: 645px) and (max-width: 934px) {
  .skills .tools-part h2 {
    margin-bottom: 8rem;
  }
}
.skills .tools-part .tools {
  grid-template-columns: repeat(auto-fit, minmax(7rem, auto));
  column-gap: 4rem;
}

/* Tooltip */
.skill h3 {
  background-color: #050505;
  color: #18db86;
  text-align: center;
  border-radius: 0.5rem;
  padding: 0.5rem 0;
  font-size: 1.1rem;
  width: 10rem;
  opacity: 0;
  transition-property: transform opacity;
  transition: 0.4s ease;
}
.skill .dos-palabras {
  padding: 0.5rem 1rem;
}
.skill .knowledge h3, .skill .skills .tools-part .tools h3, .skills .tools-part .skill .tools h3 {
  transform: translate(-0.5rem, -10rem);
}
.skill .tools h3 {
  transform: translate(-1rem, -10rem);
}
.skill .knowledge div:hover h3, .skill .skills .tools-part .tools div:hover h3, .skills .tools-part .skill .tools div:hover h3 {
  transform: translate(-0.5rem, -12.5rem);
  opacity: 1;
}
.skill .tools div:hover h3 {
  transform: translate(-1rem, -12.5rem);
  opacity: 1;
}

/* About */
.about {
  background-color: #050505;
}
.about .about-flex {
  display: flex;
  background-color: #050505;
  width: 100%;
  margin: 0 auto;
  padding: 5rem 0;
}
.about p {
  padding-right: 2rem;
  font-size: 1.5rem;
  width: 90%;
  margin: 0 0 0 auto;
  color: white;
  line-height: 2.5;
  color: rgb(173, 173, 173);
}
.about .social {
  border-left: white 0.5rem solid;
  padding-right: 5rem;
  margin-left: 0.2rem;
}
.about .social img {
  width: 6rem;
  margin: 1rem;
}
.about .social img:hover {
  transform: scale(1.2);
}
.about img {
  height: auto;
}

/* Footer */
.footer {
  background-color: #050505;
  padding: 1rem;
}
.footer p {
  text-align: center;
  color: rgb(173, 173, 173);
}

/* Form Modal */
form {
  display: flex;
  flex-direction: column;
  max-width: 40rem;
  margin: 0 auto;
}
form label {
  margin: 1rem 0 0.5rem 0rem;
  padding: 0.5rem;
}

.form > label > span {
  margin-left: 0.5rem;
}
.form > label > input, .form > label > textarea {
  background-color: #050505;
  border-radius: 0 1rem 1rem 1rem;
  padding: 1.5rem 6rem 1.5rem 2rem;
  color: white;
  outline: none;
  opacity: 0.7;
  width: 100%;
  margin: 0.5rem 0;
}
.form > label > textarea {
  padding: 1.5rem 6rem 10rem 2rem;
}
.form > button {
  background-color: #18db86;
  color: white;
  border-radius: 1rem;
  padding: 1rem;
}

.text-center {
  text-align: center;
}

.bg-principal {
  background-color: #18db86;
}

.bg-white {
  background-color: #FFF;
}

.margin-negativo-10 {
  margin-top: -10rem;
}

.guion {
  visibility: hidden;
}
@media (min-width: 768px) {
  .guion {
    visibility: visible;
  }
}